import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import MergedIVCalculatorApp from './components/main/MergedIVCalculatorApp';
import registerServiceWorker from './registerServiceWorker';
import { BrowserRouter as Router, Route } from 'react-router-dom'
import DocumentTitle from 'react-document-title'
import { BASEDOCTITLE } from './components/Constants'

ReactDOM.render(
    <DocumentTitle title={BASEDOCTITLE}>
        <Router basename={process.env.PUBLIC_URL}>
          <Route path="/" component={MergedIVCalculatorApp}/>
        </Router>
    </DocumentTitle>, document.getElementById('root')
    );
registerServiceWorker();


