import { EXCLUDEUNITS, INCLUDEUNITS } from './Constants'

// Import all image names
export function importAll(r) {
    let images = {}
    r.keys().map((item, idx) => { 
        return images[item.replace('./', '')] = r(item); 
    })
    return images
}

export function listAllFiles(r) {
    let files = []

    r.keys().map((item, idx) => {
        let removeExToken = item.replace('[EX]', '!')
        if (!checkContainsInList(EXCLUDEUNITS, removeExToken) || checkContainsInList(INCLUDEUNITS, removeExToken)) {
            return files.push(extractHeroName(item.replace('./', '')))
        }
        return files
    })
    return files
}

export function checkContainsInList (list, element) {
    var listLength = list.length
    for (let i = 0; i < listLength; i++) {
        if (element.indexOf(list[i]) >= 0) {
            return true
        }
    }
    return false
}

export function capitalizeFirstLetter(string) {
    return string.charAt(0).toUpperCase() + string.slice(1);
}

export function capitalizeAtLetter(string, idx) {
    return string.charAt(idx).toUpperCase() + string.slice(idx + 1)
}

export function extractBoonBane(string) {
    if (string === 'neutral') {
        return capitalizeFirstLetter(string)
    }
    var boonBane = {}
    const token = string.indexOf('/')
    if (token >= 0) {
        boonBane['boon'] = '+' + capitalizeAtLetter(string.substring(0, token), 1)
        boonBane['bane'] = '-' + capitalizeAtLetter(string.substring(token+1, string.length), 1)
    }
    return boonBane
}

export function getImage(heroName, token, imageSource) {
    var tokenId = token + '_' + heroName
    var selectImage = (tokenId) => {
        const keys = Object.keys(imageSource).filter(image => image.indexOf(tokenId) >= 0)
        const keyMatch = keys.sort((a, b) => a.length - b.length)[0] // Take the shortest match (i.e. closest match)
        return imageSource[keyMatch]
    }

    let removeExToken = heroName.replace('[EX]', '!')
    if (!checkContainsInList(EXCLUDEUNITS, removeExToken) || checkContainsInList(INCLUDEUNITS, removeExToken)) {
        return selectImage(tokenId)
    }
}

export function invertDuplicateValuesDict(dict) {
    var newDict = {}
    for (let key in dict) {
        let newKey = dict[key].replace(/['"]+/g, '')
        if (dict[key] in newDict) {
            newDict[newKey].push(key)
        } else {
            newDict[newKey] = [key]
        }
    }
    return newDict
}

/* Extract hero name from the file name */
export function extractHeroName(fileName) {

    let underscoreTokenIdx = fileName.lastIndexOf('_')
    let fileExtTokenIdx = fileName.indexOf('.')
    let beginIdx = 0
    let endIdx = fileName.length
    if (underscoreTokenIdx >= 0) {
        beginIdx = underscoreTokenIdx + 1
    }
    if (fileExtTokenIdx >= 0) {
        endIdx = fileExtTokenIdx
    }

    // Revert the exclamation points
    return fileName.substring(beginIdx, endIdx).replace('[EX]', '!')
}

/* Flip the '!' token to '[EX]' */
export function invertHeroName(heroName) {
    return heroName.replace('!', '[EX]')
}

export function isEmpty(obj) {
  return Object.keys(obj).length === 0;
}


